import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { VendorService } from '../vendor.service';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/guards/auth.service';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ViewContactComponent implements OnInit {

  vId: any;
  vendor = new Vendor();
  constructor(private _service: VendorService, private route: ActivatedRoute, private authService: AuthService) { }

  ngOnInit() {
    this.getVendorById();
  }

  getVendorById() {
    this.vId = this.route.snapshot.paramMap.get('id');
    this._service.getVendorId(this.vId)
      .subscribe((data) => {
        this.vendor = data;
        console.log('inside');
        console.log(data);

        console.log(this.vendor.vendorName);
      }, (error) => {
        console.log(error);
      })
  }

  logout() {
    this.authService.logout();
  }
}
