import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from '../vendor.service';
import { AuthService } from 'src/app/guards/auth.service';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  vendor = new Vendor();
  vendors: Vendor[];
  vendorId: any;

  constructor(private router: Router, private route: ActivatedRoute, private _service: VendorService, private authService: AuthService) { }

  ngOnInit() {
    this.getVendorById();
  }

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      vendorAddress: '',
      vendorLocation: '',
      vendorService: '',
      vendorPincode: '',
      isActive: '',
      contactId: '',
      contactName: '',
      contactDepartment: '',
      contactEmail: '',
      contactPhone: '',

    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
      .subscribe((response) => {

        console.log(response);
        // this.toastr.success('Saved successfully');
        this.router.navigate(['vendor'])
      }, (error) => {
        console.log(error);
      });
  }
  getVendor(): void {
    this._service.getAllVendors()
      .subscribe((data) => {
        this.vendors = data;
        console.log(data);
      }, (error) => {
        console.log(error);
      });

  }


  getVendorById() {
    this.vendorId = this.route.snapshot.paramMap.get('id');
    this._service.getVendorId(this.vendorId)
      .subscribe((data) => {
        this.vendor = data;
        console.log(data);
      }, (error) => {
        console.log(error);
      })
  }

  logout() {
    this.authService.logout();
  }

}
